.container {
  width: 100%;
  height: 100%;
  position: relative;
}

.container img {
  width: 100%;
  height: 100%;
}

.loaded {
  display: none;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading i {
  color: rgb(255, 255, 255);
}